:root {
  --body-background: #d6c5ae;
  --warm-brown-light: #c2a885;
  --warm-brown-dark-opacity: #2d211086;
  --warm-brown-dark: #2d2110;
  --calm-blue-10: #0f396b;
  --calm-blue-hover: #0a85d6;
  --forest-green-magic: #2f6d22;
  --forest-green-magic-dark: #062;
  --accent-2: #8a240f;
  --main-family: "Lora", serif;
  --sub-family: "Oswald", sans-serif;
  --about-section-margin-top: 70vh;
  --section-margin-top: 30rem;
}

* {
  box-sizing: border-box;
  border: 0 solid;
  margin: 0;
  padding: 0;
}

:before, :after {
  padding: inherit;
  margin: inherit;
  box-sizing: border-box;
  border: 0 solid;
}

html {
  font-size: 62.5%;
  font-family: var(--main-family);
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  background-color: var(--body-background);
  max-width: 140rem;
  position: relative;
  overflow-x: hidden;
}

.grid-container {
  grid-template-columns: [full-start] minmax(6rem, 1fr)[centre-start] repeat(8, [col-start] minmax(min-content, 14rem)[col-end])[centre-end] minmax(6rem, 1fr)[full-end];
  display: grid;
}

.btn {
  background-image: linear-gradient(270deg, var(--calm-blue-10) 0%, var(--calm-blue-10) 50%, var(--calm-blue-hover) 50%, var(--calm-blue-hover) 100%);
  text-align: center;
  background-position: 100% 100%;
  background-size: 200%;
  border-radius: 3rem;
  transition: all .3s;
  display: inline-block;
}

.btn:hover, .btn:active {
  background-position: 0;
}

.btn:active {
  box-shadow: none;
  transform: translateY(.2rem);
}

.display-none {
  display: none;
}

.overlay {
  -webkit-backdrop-filter: blur(1px);
  backdrop-filter: blur(1px);
  z-index: 1000;
  background-color: #0009;
  width: 100%;
  height: 100%;
  transition: all .5s;
  position: fixed;
  top: 0;
  left: 0;
}

.blue-highlight {
  color: var(--calm-blue-10);
}

.success, .error {
  color: #fff;
  font-size: 1.8rem;
  font-family: var(--sub-family);
  letter-spacing: .06rem;
  text-align: center;
  z-index: 20000;
  line-height: 1.5;
  display: block;
  position: fixed;
  top: -12rem;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 10px 15px -3px #0000001a, 0 4px 6px -4px #0000001a;
}

.success {
  background-color: var(--forest-green-magic-dark);
  width: 40rem;
  height: 10rem;
  padding: 2rem;
  transition: all .5s ease-in;
}

@media only screen and (width <= 27.75em) {
  .success {
    width: 30rem;
    font-size: 1.6rem;
  }
}

@media only screen and (width <= 20.44em) {
  .success {
    width: 27rem;
    font-size: 1.4rem;
  }
}

@media only screen and (width <= 17em) {
  .success {
    width: 20rem;
    font-size: 1.2rem;
    font-weight: 500;
  }
}

.error {
  background-color: var(--accent-2);
  width: 30rem;
  padding: 3rem;
  transition: all .8s ease-in;
}

section {
  position: relative;
}

p {
  max-width: 55ch;
}

a {
  color: inherit;
  text-decoration: none;
  display: block;
}

ul {
  list-style: none;
  display: inline-block;
}

li {
  display: inline-block;
}

img {
  object-fit: cover;
  max-width: 100%;
  max-height: 100%;
  display: block;
}

.navigation {
  font-family: var(--main-family);
  z-index: 999;
  grid-column: full-start / full-end;
  justify-content: center;
  align-items: center;
  min-height: 15vh;
  margin-top: 1.5rem;
  font-size: 1.4rem;
  font-style: italic;
  display: flex;
  position: relative;
}

.navigation__logo {
  width: 30%;
  height: 30%;
  margin-left: 2rem;
  position: absolute;
  top: 20%;
  left: 3%;
  overflow: visible;
  transform: translate(-50%, -50%);
}

.navigation__links {
  background-color: var(--warm-brown-light);
  border-radius: 4rem;
  justify-content: space-around;
  align-items: center;
  width: 50%;
  padding: 1rem 3.2rem;
  display: flex;
  position: relative;
}

@media only screen and (width <= 73.7em) {
  .navigation__links {
    width: 60%;
  }
}

@media only screen and (width <= 52.85em) {
  .navigation__links {
    display: none;
  }
}

.navigation__links--items a {
  color: var(--warm-brown-dark);
  letter-spacing: .04rem;
  font-weight: 700;
  transition: all .3s;
}

.navigation__links--items a:hover {
  color: #986e34;
}

.navigation__btn {
  border: .5rem solid var(--body-background);
  width: 25%;
  padding: .8rem .16rem;
  font-size: 1.6rem;
  position: absolute;
  top: -40%;
  right: -12%;
}

@media only screen and (width <= 60.85em) {
  .navigation__btn {
    right: -14%;
  }
}

.navigation span {
  color: #fff;
}

.opening-hours {
  position: relative;
}

.hours {
  background-color: var(--calm-blue-10);
  z-index: 10;
  text-align: center;
  color: #fff;
  visibility: hidden;
  background-image: url("Clarence-background.218f7f7b.png");
  background-size: cover;
  border-radius: 1.9rem;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 17rem;
  height: 15rem;
  font-size: 1.5rem;
  font-weight: 500;
  display: flex;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, 10%);
  box-shadow: 0 4px 6px -1px #0000001a, 0 2px 4px -2px #0000001a;
}

.opening-hours:hover .hours, .opening-hours:active .hours {
  visibility: visible;
}

